html {
  font-family: Roboto, sans-serif;
}

body, h3, p {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  justify-content: center;
  margin-right: 20px;
  list-style: none;
  display: flex;
}

.nav__item:last-of-type {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  border: 1% solid #00acdc;
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.container {
  grid-template-columns: repeat(1, cardWidth);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  padding-inline: 16px;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding-bottom: 16px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__product {
  margin-bottom: 40px;
  padding: 0;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 32px auto 0;
  display: block;
}

.card__label {
  text-align: left;
  color: #060b35;
  width: 100%;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__label:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 16px;
  font-family: inherit;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  align-items: center;
  margin-left: 19px;
  font-family: inherit;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__cost {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price {
  text-align: left;
  color: #616070;
  font-family: inherit;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__money {
  text-align: right;
  color: #060b35;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
}

.card__buy {
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__buy:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.card__rating {
  margin-bottom: 24px;
  display: flex;
}

.card__stars {
  display: flex;
}

.stars__star {
  float: right;
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: block;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
